import { CommonModule } from "@angular/common";
import { NgModule, ModuleWithProviders, NO_ERRORS_SCHEMA, CUSTOM_ELEMENTS_SCHEMA } from "@angular/core";
import { FormsModule, ReactiveFormsModule } from "@angular/forms";
import { CmxDatepickerModule } from "@cemex/cmx-datepicker-v4";
import { CmxFormFieldComponent } from "./components/cmx-form-field";
import { CmxOptionComponent } from "./components/cmx-option";
import { CmxSelectComponent } from "./components/cmx-select";
import { DatepickerInputComponent } from "./components/datepicker-input";
import { NumberInputComponent } from "./components/number-input";
import { TimeInputComponent } from "./components/time-input";
import { ErrorMessageDirective } from "./directives";
import { DateFormatService } from "./services";
import { CmxCheckboxModule } from "@cemex/cmx-checkbox-v4";
import { FormatterService } from "./services/formatter.service";
import { CurrencyMaskModule } from "./components/number-input/currency-mask.module";

@NgModule({
    declarations: [
        CmxFormFieldComponent,
        CmxOptionComponent,
        CmxSelectComponent,
        DatepickerInputComponent,
        NumberInputComponent,
        TimeInputComponent,
        ErrorMessageDirective,
    ],
    exports: [
        CmxFormFieldComponent,
        CmxOptionComponent,
        CmxSelectComponent,
        DatepickerInputComponent,
        NumberInputComponent,
        TimeInputComponent,
        ErrorMessageDirective,
    ],
    imports: [
        CommonModule,
        FormsModule,
        ReactiveFormsModule,
        CmxDatepickerModule,
        CmxCheckboxModule,
        CurrencyMaskModule,
    ],
    providers: [
        DateFormatService,
    ],
    schemas: [
        CUSTOM_ELEMENTS_SCHEMA,
        NO_ERRORS_SCHEMA,
      ],
})
export class CmxFormFieldModule {

    public static forRoot(formatter: any): ModuleWithProviders {
        return {
          ngModule: CmxFormFieldModule,
          providers: [
            { provide: FormatterService, useExisting: formatter },
          ],
        };
      }
}



